
html,
body {
  font-family: "Quicksand", sans-serif;
}
body {
  padding: 0;
  margin: 0;
}
:root {
  --main: #18191c;
  --main-accent: #f5bc60;
  --about: #050505;
  --about-accent: #23cf5f;
  --showcase: #23263d;
  --showcase-accent: #e91e63;
  --light: #fffcff;
  --text-color: var(--light);
  --block-shadow: 0 1px 3px rgba(0, 0, 0, 0.4);
  --b-r: 5px;
  --accent: var(--main-accent);
  --dark: #0c0c0c;
  --warning: #cf5c36;
}
body * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
h2,
h3,
h4 {
  margin: 10px 0;
}
#app {
  height: 100vh;
  width: 100vw;
  max-height: 100%;
  overflow-y: auto;
  overflow-y: overlay;
  -webkit-transition: background-color 0.3s 0.2s;
  transition: background-color 0.3s 0.2s;
  background-color: #18191c;
  position: relative;
}
#app::-webkit-scrollbar {
    display: none;
}
#app *::-moz-selection {
    background: transparent;
    color: var(--accent);
}
#app *::selection {
    background: transparent;
    color: var(--accent);
}
.main {
  position: relative;
  top: 15vh;
  max-width: 900px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  -webkit-transition: -webkit-filter 1s;
  transition: -webkit-filter 1s;
  transition: filter 1s;
  transition: filter 1s, -webkit-filter 1s;
  border-radius: var(--b-r);
}
.main > section {
    position: relative;
}
.main * {
    -webkit-perspective: 300px;
            perspective: 300px;
}
@-webkit-keyframes fade {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fade {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@media screen and (max-width: 600px) {
.main {
    top: 12vh;
    padding-bottom: 50px;
}
}

.nav {
  background-color: var(--nav-bg, transparent);
  width: 100%;
  height: 50px;
  position: fixed;
  left: 0;
  z-index: 100;
  border-top-left-radius: 4px;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.nav-appear .nav-link {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
    -webkit-animation: showNavLinks 1.2s ease-out;
            animation: showNavLinks 1.2s ease-out;
}
.nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.nav .nav-link {
    display: inline-block;
    height: 50px;
    margin: 0 5px;
    padding: 5px;
    -webkit-transition: background-color 0.3s, -webkit-backdrop-filter 0.5s;
    transition: background-color 0.3s, -webkit-backdrop-filter 0.5s;
    transition: backdrop-filter 0.5s, background-color 0.3s;
    transition: backdrop-filter 0.5s, background-color 0.3s, -webkit-backdrop-filter 0.5s;
}
.nav .nav-link:hover {
      background-color: var(--nav-link-hover-bg, transparent);
}
.nav .nav-link:hover a {
        opacity: 1;
}
.nav .nav-link a {
      display: block;
      width: 100%;
      height: 100%;
      -webkit-transition: color 0.3s, opacity 0.3s;
      transition: color 0.3s, opacity 0.3s;
      opacity: 0.8;
      color: var(--text-color);
      text-align: center;
      text-decoration: none;
      font-weight: 500;
      font-size: 18px;
      line-height: 38px;
      padding: 0 10px;
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
}
.nav .nav-link.router-link-exact-active a {
      opacity: 1;
      color: var(--accent);
}
.nav .pointer {
    display: block;
    height: 2px;
    width: 10px;
    position: absolute;
    bottom: 0;
    left: 0;
    -webkit-transition: -webkit-transform 0.6s ease;
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
    background-color: var(--accent);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
}
@-webkit-keyframes showNavLinks {
0% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
40% {
    opacity: 1;
}
70% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    opacity: 1;
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@keyframes showNavLinks {
0% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
40% {
    opacity: 1;
}
70% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    opacity: 1;
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@media screen and (max-width: 600px) {
.nav {
    --bg: var(--nav-bg, var(--main));
    top: auto;
    bottom: 0;
    width: 100%;
    background-color: var(--bg);
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
}
.nav .pointer {
      bottom: auto;
      top: 0;
}
}

.page-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  min-height: 500px;
  background-color: transparent;
}
.page-main > * {
    display: block;
    width: 100%;
    position: relative;
}
.heading {
  font-size: 80px;
  margin: 10% 4rem 0 0;
  text-align: right;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
  color: var(--accent);
  font-family: "Dosis", sans-serif;
}
.heading > span {
    opacity: 0;
    -webkit-animation: fade 3s forwards;
            animation: fade 3s forwards;
}
.desc {
  font-size: 30px;
  text-align: right;
  padding: 0;
  margin: 10px 40px 0 0;
  color: var(--text-color);
}
.social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  position: absolute;
  bottom: 0;
}
.link {
  position: relative;
  padding: 10px;
  margin: 10px;
  display: inline-block;
  width: 50px;
  height: 50px;
}
.link:hover svg {
  fill: var(--accent);
}
.link svg {
  width: 100%;
  height: 100%;
  fill: var(--text-color);
  -webkit-transition: fill 0.4s;
  transition: fill 0.4s;
}
.main-enter-active {
  -webkit-transition: opacity 0.8s;
  transition: opacity 0.8s;
}
.main-enter-active .heading,
  .main-enter-active .desc {
    -webkit-transition: opacity 0.6s, -webkit-transform 0.8s;
    transition: opacity 0.6s, -webkit-transform 0.8s;
    transition: transform 0.8s, opacity 0.6s;
    transition: transform 0.8s, opacity 0.6s, -webkit-transform 0.8s;
}
.main-enter-active .social {
    -webkit-transition: opacity 0.5s, -webkit-transform 1s;
    transition: opacity 0.5s, -webkit-transform 1s;
    transition: transform 1s, opacity 0.5s;
    transition: transform 1s, opacity 0.5s, -webkit-transform 1s;
}
.main-enter .heading {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}
.main-enter .desc {
  opacity: 0;
}
.main-enter .social {
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
}
.showcase-leave-active,
.main-leave-active {
  -webkit-transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: transform 0.5s, opacity 0.5s;
  transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
}
.showcase-leave-active .heading,
  .showcase-leave-active .social,
  .main-leave-active .heading,
  .main-leave-active .social {
    -webkit-transition: -webkit-transform 0.4s;
    transition: -webkit-transform 0.4s;
    transition: transform 0.4s;
    transition: transform 0.4s, -webkit-transform 0.4s;
}
.showcase-leave-to,
.main-leave-to {
  opacity: 0;
}
.showcase-leave-to .heading,
  .main-leave-to .heading {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}
.showcase-leave-to .social,
  .main-leave-to .social {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}
@media screen and (max-width: 760px) {
.heading,
  .desc {
    margin-right: 5px;
}
.heading {
    font-size: 40px;
}
.desc {
    font-size: 24px;
}
}
@media screen and (max-width: 600px) {
.page-main {
    min-height: 400px;
}
.container {
    padding-right: 10px;
}
}

.about {
  color: var(--text-color);
  min-height: 600px;
  top: -5vh;
}
.about > header {
    width: 100%;
    position: relative;
    padding: 0 60px;
    margin-bottom: 40px;
}
.about > header p {
      font-size: 24px;
      padding: 0;
      margin: 0;
}
.s-card {
  -webkit-transition: -webkit-box-shadow 0.4s, -webkit-transform 0.4s;
  transition: -webkit-box-shadow 0.4s, -webkit-transform 0.4s;
  transition: box-shadow 0.4s, transform 0.4s;
  transition: box-shadow 0.4s, transform 0.4s, -webkit-box-shadow 0.4s, -webkit-transform 0.4s;
}
.skills-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 8px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.skills-grid li {
    height: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    overflow: hidden;
    -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
            box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
    -webkit-transition: opacity 0.2s, -webkit-transform 0.4s;
    transition: opacity 0.2s, -webkit-transform 0.4s;
    transition: transform 0.4s, opacity 0.2s;
    transition: transform 0.4s, opacity 0.2s, -webkit-transform 0.4s;
    background: rgba(255, 255, 255, 0.1);
}
.skills-grid li:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: radial-gradient(rgba(255, 255, 255, 0.1), var(--about));
      -webkit-transition: opacity 0.4s;
      transition: opacity 0.4s;
      opacity: 0.2;
}
.skills-grid li:hover:before {
      opacity: 1;
}
.skills-grid li:hover .s-name {
      opacity: 0;
      -webkit-transform: translateY(-10%);
              transform: translateY(-10%);
}
.skills-grid li:hover .s-short {
      opacity: 1;
      -webkit-transform: translateY(-100%);
              transform: translateY(-100%);
}
.s-name {
  font-size: 18px;
  padding: 41px 20px;
  text-align: center;
  line-height: 18px;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, -webkit-transform 0.3s;
}
.s-short {
  padding: 4px;
  opacity: 0;
  font-size: 14px;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, -webkit-transform 0.3s;
}
.about-enter,
.about-leave-to {
  opacity: 0;
}
.about-enter > header,
  .about-leave-to > header {
    border-color: white;
}
.about-enter > header p,
    .about-leave-to > header p {
      opacity: 0;
      -webkit-filter: grayscale(1);
              filter: grayscale(1);
}
.about-enter .skills-grid > li,
  .about-leave-to .skills-grid > li {
    opacity: 0;
    -webkit-transform: translate3d(-20px, 50px, 0);
            transform: translate3d(-20px, 50px, 0);
}
.about-leave-active,
.about-enter-active {
  -webkit-transition: background-color 0.7s, opacity 0.3s;
  transition: background-color 0.7s, opacity 0.3s;
}
.about-leave-active > header,
  .about-enter-active > header {
    -webkit-transition: border-color 0.7s;
    transition: border-color 0.7s;
}
.about-leave-active > header p,
    .about-enter-active > header p {
      -webkit-transition: opacity 0.3s, -webkit-filter 0.7s 0.2s;
      transition: opacity 0.3s, -webkit-filter 0.7s 0.2s;
      transition: opacity 0.3s, filter 0.7s 0.2s;
      transition: opacity 0.3s, filter 0.7s 0.2s, -webkit-filter 0.7s 0.2s;
}
@media screen and (max-width: 600px) {
.about {
    padding: 0 1rem;
    -webkit-perspective: 2000px;
            perspective: 2000px;
}
}

.showcase-enter {
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
  opacity: 0;
}
.showcase-enter .card-promise {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
}
.showcase-enter-active {
  -webkit-transition: opacity 0.4s, -webkit-transform 1s ease;
  transition: opacity 0.4s, -webkit-transform 1s ease;
  transition: transform 1s ease, opacity 0.4s;
  transition: transform 1s ease, opacity 0.4s, -webkit-transform 1s ease;
}
.showcase-enter-active .card-promise {
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
